.form {
  .form-group {
    display: block;
    margin-bottom: 1em;
  }

  label {
    display: block;
    margin-bottom: .25em;
    color: $teal;
    font-weight: 600;

    span {
      display: block;
      font-weight: 400;
      color: $gray_600;
      font-size: .9rem;
      padding: .125em 0 0 0;
    }
  }

  textarea,
  input[type="text"] {
    border-radius: 4px;
    border: 1px solid adjust-color($purple, $lightness: 50%, $saturation: -10%);
    background: adjust-color($purple, $lightness: 70%, $saturation: -10%);
    transition: .2s all ease;
    outline: none;
    display: block;
    width: 100%;
    font-size: 1em;
    color: $base-font-color;
    box-sizing: border-box;

    &:focus {
      background: #fff;
      border-color: $teal;
      box-shadow: 0px 10px 15px -15px rgba($purple, .25);
    }
  }

  textarea {
    min-height: 100px;
    resize: vertical;
    padding: .75em;
  }

  input[type="text"] {
    height: 36px;
    padding: 0 .75em;
  }
}


.checkbox {
  position: absolute;
  left: -9999px;

  &+ label {
    position: relative;
    cursor: pointer;
    display: flex;
    align-items: flex-end;
    justify-content: flex-start;
    font-weight: normal;
    font-size: 1em;
    margin: 0;

    &:before {
      content: '';
      position: relative;
      width: 1.25em;
      height: 1.25em;
      display: inline-block;
      border: 1px solid $gray_500;
      background: $gray_300;
      border-radius: 4px;
      box-shadow: inset 0 1px 3px rgba(0,0,0,.1);
      margin: 0 .5em 0 0;
      transition: .2s all ease;
    }

    &:after {
      content: '';
      width: calc(1.25em + 2px);
      height: calc(1.25em + 2px);
      position: absolute;
      top: 0;
      left: 0;
      background-image: url(data:image/svg+xml;base64,);
      display: block;
      opacity: 0;
      transform: scale(0);
      transition: .1s all ease;
    }
  }
}

.checkbox:not(:checked):hover {
  &+ label:before {
    background: darken($gray_300, 4%);
  }
}

.checkbox:checked {
  &+ label {
    &:before {
      background: $teal;
      border-color: darken($teal, 3%);
    }

    &:after {
      opacity: 1;
      transform: scale(.7);
    }
  }
}

.checkbox:disabled {
  &+ label {
    color: $gray_500;
    cursor: not-allowed;

    &:before {
      background: $gray_200 !important;
      border-color: $gray_300 !important;
      cursor: not-allowed;
    }

    &:after {
      display: none;
    }
  }
}

.checkbox.checkbox-sm {
  &+ label {
    font-size: .95em;

    &:before {
      width: 1em;
      height: 1em;
      border-radius: 3px;
    }

    &:after {
      width: calc(1em + 2px);
      height: calc(1em + 2px);
    }
  }

  &:checked {
    &+ label:after {
      transform: scale(.65);
    }
  }
}
